// secondary components:
import {
	Button,
	TextField,
	Paper,
	Dialog,
	DialogActions,
	DialogContent,
	DialogTitle,
} from "@material-ui/core";

// material ui:
import { withStyles } from "@material-ui/core/styles";

import DayPicker, { DateUtils } from "react-day-picker";
import "react-day-picker/lib/style.css";

// recompose:
import { compose } from "recompose";

// device sizes:
import { device } from "party/constants/DeviceSizes.js";

// libs:
import localforage from "localforage";
window.localforage = localforage;
import swal from "sweetalert2";
window.swal = swal;

const GREEN_HIGHLIGHT = "#1af434";
const RED_HIGHLIGHT = "#f6230c";
const BLUE_HIGHLIGHT = "#1e56f0";
// const YELLOW_HIGHLIGHT = "#a0af17";

// jss:
const styles = {
	root: {
		display: "flex",
		flexDirection: "column",
		justifyContent: "center",
		height: "100vh",
		fontSize: "10px",
		// margin: "1%",
	},
	[device.tablet]: {
		root: {
			fontSize: "20px",
		},
	},
	[device.laptop]: {
		root: {},
	},

	mainContainer: {
		display: "flex",
		flexDirection: "row",
		justifyContent: "space-evenly",
		height: "500px",
	},

	textField: {
		margin: "0px",
	},
	formPart: {
		display: "flex",
		flexDirection: "row",
		justifyContent: "space-evenly",
		// width: "50%",
		height: "min-content",
	},
	exportButton: {
		marginTop: "10px",
	},

	legend: {
		display: "flex",
		flexDirection: "row",
		margin: "0 auto",
		fontSize: "20px",
	},
};

function deserializeDates(dates) {
	const dateTsArr = dates.map((d) => new Date(d));
	return dateTsArr;
}

const currentYear = new Date().getFullYear();
const fromMonth = new Date(currentYear, 0);
const toMonth = new Date(currentYear + 10, 11);

function YearMonthForm({ date, localeUtils, onChange }) {
	const months = localeUtils.getMonths();

	const years = [];
	for (let i = fromMonth.getFullYear(); i <= toMonth.getFullYear(); i += 1) {
		years.push(i);
	}

	const handleChange = (e) => {
		const { year, month } = e.target.form;
		onChange(new Date(year.value, month.value));
	};

	return (
		<form className="DayPicker-Caption">
			<select
				style={{
					width: "55%",
					height: "40px",
					fontSize: "27px",
				}}
				name="month"
				onChange={handleChange}
				value={date.getMonth()}
			>
				{months.map((month, i) => (
					<option key={month} value={i}>
						{month}
					</option>
				))}
			</select>
			<select
				style={{
					width: "30%",
					height: "40px",
					fontSize: "27px",
				}}
				name="year"
				onChange={handleChange}
				value={date.getFullYear()}
			>
				{years.map((year) => (
					<option key={year} value={year}>
						{year}
					</option>
				))}
			</select>
		</form>
	);
}

class Room extends PureComponent {
	constructor(props) {
		super(props);
		this.socket = null;

		this.state = {
			username: "",
			roomName: "",
			openUsernameDialog: true,
			isHost: false,

			potentialDays: [],
			canDays: [],
			cantDays: [],
			userSubmissions: {},

			month: fromMonth,
			num: 0,
		};

		this.roomName = null;
	}

	componentDidMount() {
		// // listen to events and dispatch actions:
		// handleStreamEvents(this.socket, this.props.store.dispatch);
		// // handle outgoing events & listen to actions:
		// // and maybe dispatch more actions:
		// this.props.sagaMiddleware.run(handleStreamActions, {
		// 	socket: this.socket,
		// 	dispatch: this.props.store.dispatch,
		// });

		this.socket = this.props.serverConnection;

		this.roomName = this.props.match.params.roomName;

		if (!this.roomName) {
			console.log("room name not set!");
		}

		this.socket.on("songList", (data) => {
			// this.songList = data.songList;
			// this.setState({ songList: data.songList });
			this.props.updateSongList(data.songList);
			// this.props.store.dispatch(updateSongList({ songList: data.songList }));
		});
	}

	componentWillUnmount() {}

	// shouldComponentUpdate(nextProps, nextState) {
	// 	return false;
	// }

	handleSubmitUsername = () => {
		if (this.state.username === "") {
			alert("username can't be empty");
			return;
		}

		this.socket.emit(
			"joinRoom",
			{ roomName: this.roomName, username: this.state.username },
			(data) => {
				if (data.success) {
					this.setState({ openUsernameDialog: false });
					if (data.isHost) {
						this.setState({ isHost: true });
						alert(
							"You are the Host! Please pick potential dates for the party and submit them!",
						);

						if (data.userSubmissions) {
							this.setState({ userSubmissions: data.userSubmissions });
						}
					}
					if (data.potentialDays) {
						let days = deserializeDates(data.potentialDays);
						this.setState({ potentialDays: days });
						if (days.length > 0) {
							this.setState({ month: days[0] });
						}
						if (!data.isHost) {
							alert("Please select which dates you can and can't go!");
						}
					}
				} else {
					alert(data.reason);
				}
			},
		);
	};

	handleText = (event) => {
		this.setState({ username: event.target.value });
	};

	handleSubmitDates = () => {
		// this.socket.emit("createPlaylist", null, (data) => {
		// 	if (!data.success) {
		// 		console.log(data.reason);
		// 	} else {
		// 		console.log(data.playlist);
		// alert("successfully sent date data!");
		// 	}
		// });

		if (this.state.isHost) {
			this.socket.emit(
				"submitPotentialDays",
				{ potentialDays: this.state.potentialDays },
				(data) => {
					if (!data.success) {
						console.log(data.reason);
					} else {
						alert("successfully sent date data!");
					}
				},
			);
		} else {
			this.socket.emit(
				"submitDays",
				{ canDays: this.state.canDays, cantDays: this.state.cantDays },
				(data) => {
					if (!data.success) {
						console.log(data.reason);
					} else {
						alert("successfully sent date data!");
					}
				},
			);
		}
	};

	handleResults = () => {

		console.log(this.state.userSubmissions);

		let scores = {};
		for (let key in this.state.userSubmissions) {
			let sub = this.state.userSubmissions[key];

			for (let i = 0; i < sub.canDays.length; i++) {
				let d = sub.canDays[i];
				console.log(d);
				if (typeof scores[d] === "undefined") {
					scores[d] = 1;
				} else {
					scores[d] += 1;
				}
			}

			for (let i = 0; i < sub.cantDays.length; i++) {
				let d = sub.cantDays[i];
				if (typeof scores[d] === "undefined") {
					scores[d] = -1;
				} else {
					scores[d] -= 1;
				}
			}
		}

		let scores2 = [];

		for (let key in scores) {
			let score = scores[key];

			scores2.push({ date: key, score: score });
		}

		scores2.sort((a, b) => (a.score < b.score ? 1 : -1));

		let results = "scores: ";

		for (let i = 0; i < 3; i++) {
			if (scores2[i]) {
				results += `{${new Date(scores2[i].date).toLocaleDateString({year: "none"})}: ${scores2[i].score}} `;
			}
		}
		// console.log(scores2);
		alert(results);
	};

	handleYearMonthChange = (date) => {
		this.setState({ month: date });
	};

	handleDayClick = (day, { selected }) => {
		const { potentialDays, canDays, cantDays } = this.state;

		if (this.state.isHost) {
			if (selected) {
				const selectedIndex = potentialDays.findIndex((selectedDay) =>
					DateUtils.isSameDay(selectedDay, day),
				);
				potentialDays.splice(selectedIndex, 1);
			} else {
				potentialDays.push(day);
			}
			this.setState({ potentialDays });
		} else {
			const canDayIndex = canDays.findIndex((selectedDay) =>
				DateUtils.isSameDay(selectedDay, day),
			);

			const cantDayIndex = cantDays.findIndex((selectedDay) =>
				DateUtils.isSameDay(selectedDay, day),
			);

			const potentialDayIndex = potentialDays.findIndex((selectedDay) =>
				DateUtils.isSameDay(selectedDay, day),
			);

			if (potentialDayIndex === -1) {
				return;
			}

			if (canDayIndex !== -1) {
				canDays.splice(canDayIndex, 1);
				cantDays.push(day);
				this.setState({ canDays, cantDays });
			} else if (cantDayIndex !== -1) {
				cantDays.splice(cantDayIndex, 1);
				this.setState({ cantDays });
			} else {
				canDays.push(day);
				this.setState({ canDays });
			}
		}

		this.setState({ num: Math.random() * Math.random() * Math.random() });
	};

	render() {
		console.log("re-rendering party-room.");

		const { classes } = this.props;

		let modifiers = {
			selected: this.state.potentialDays,
			can: this.state.canDays,
			cant: this.state.cantDays,
		};
		let modifiersStyles = {
			selected: {
				color: "white",
				backgroundColor: BLUE_HIGHLIGHT,
			},
			can: {
				color: "white",
				backgroundColor: GREEN_HIGHLIGHT,
			},
			cant: {
				color: "white",
				backgroundColor: RED_HIGHLIGHT,
			},
		};

		return (
			<div className={classes.root}>
				<div className={classes.legend}>
					<Paper
						elevation={4}
						style={{ padding: "5px", margin: "0 3px", backgroundColor: BLUE_HIGHLIGHT }}
					>
						Potential Party Date
					</Paper>
					<Paper
						elevation={4}
						style={{ padding: "5px", margin: "0 3px", backgroundColor: GREEN_HIGHLIGHT }}
					>
						Can Go
					</Paper>
					<Paper
						elevation={4}
						style={{ padding: "5px", margin: "0 3px", backgroundColor: RED_HIGHLIGHT }}
					>
						Can't Go
					</Paper>
				</div>

				<div
					className="YearNavigation"
					style={{ width: "min-content", margin: "0 auto" }}
				>
					<DayPicker
						// showOutsideDays
						onDayClick={this.handleDayClick}
						selectedDays={this.state.potentialDays}
						modifiers={modifiers}
						modifiersStyles={modifiersStyles}
						month={this.state.month}
						fromMonth={fromMonth}
						toMonth={toMonth}
						captionElement={({ date, localeUtils }) => (
							<YearMonthForm
								date={date}
								localeUtils={localeUtils}
								onChange={this.handleYearMonthChange}
							/>
						)}
					/>
				</div>
				<div style={{ display: "flex", justifyContent: "center" }}>
					<Button
						className={classes.exportButton}
						onClick={this.handleSubmitDates}
						color="primary"
						variant="contained"
						style={{ margin: "0 3px" }}
					>
						Submit Party Dates
					</Button>
					{this.state.isHost && (
						<Button
							className={classes.exportButton}
							onClick={this.handleResults}
							color="secondary"
							variant="contained"
							style={{ margin: "0 3px" }}
						>
							Show Results
						</Button>
					)}
				</div>
				<Dialog
					open={this.state.openUsernameDialog}
					onClose={() => {}}
					fullWidth={true}
					maxWidth="sm"
				>
					<DialogTitle id="alert-dialog-title">Type your name</DialogTitle>
					<DialogContent>
						<TextField
							autoFocus
							margin="dense"
							id="name"
							label="Full Name"
							type="username"
							value={this.state.username}
							onChange={this.handleText}
							onKeyPress={(event) => {
								if (event.key == "Enter") {
									this.handleSubmitUsername();
								}
							}}
							fullWidth
						/>
					</DialogContent>
					<DialogActions>
						<Button
							onClick={this.handleSubmitUsername}
							color="primary"
							variant="contained"
						>
							Submit
						</Button>
					</DialogActions>
				</Dialog>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		searchResults: state.party.songs.searchResults,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		updateSongList: (data) => {
			dispatch(updateSongList(data));
		},
	};
};

export default compose(
	withRouter,
	withStyles(styles),
	connect(mapStateToProps, mapDispatchToProps),
)(Room);
